.MemContainer {
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: max-content;
}

.MemContent {
  background-color: #feeac5;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin-top: 2%;
  margin-bottom: 2%;
  padding: 2% 5% 2% 5%;
  border: 1px solid #000000;
  width: max-content;
  height: max-content;
}

.MemImage {
  height: max-content;
  width: max-content;
  margin-top: 2%;
  margin-bottom: 2%;
}

.EnterButton {
  background: #ffffff;
  border: 2px solid #000000;
  border-radius: 60px;
}

.NextButton {
  border-style: none;
  background: #feeac5;
  color: black;
  font-family: Roboto, serif;
  font-style: normal;
  font-weight: 400;
  font-size: 32px;
  line-height: 38px;
  margin-bottom: 0;
}
.CreateButton {
  background: #d7a6d9;
  border: 3px solid #000000;
  box-shadow: -5px 5px 0 #000000;
  border-radius: 60px;
  font-family: Roboto, serif;
  font-style: normal;
  font-weight: 400;
  font-size: 32px;
  line-height: 38px;
  color: #000000;
}

