.nav {
  background-color: #fee9c6;
  /* Size */
  width: 100%;
  height: fit-content;

  /* Display */
  display: flex;
  flex-direction: row;
  align-content: center;
  justify-content: flex-start;
  align-items: center;

  /* Others */
  border-bottom: 2px solid #000000;
}

.logoBox {
  background-color: #fee9c6;

  height: fit-content;
  width: fit-content;
  /* Display */
  display: flex;
  flex-direction: row;
  align-content: center;
  justify-content: center;
  align-items: center;
  place-content: center;
  flex-wrap: nowrap;
}

.logoText {
  background-color: #fee9c6;
  color: #000000;
  font-family: "Parisienne", serif;
  font-size: 48px;

  /* Size */
  height: fit-content;
  width: fit-content;

  /* Display */
  display: flex;
  flex-direction: row;
  align-content: center;
  justify-content: center;
  align-items: center;
  place-content: center;
  flex-wrap: nowrap;

  /* Others */
  margin: 0 0 0 15px;
  padding: 0;
}

.HomeButton {
  background-color: #fee9c6;

  border: 1px solid #000000;
  width: 128px;

  font-family: "Roboto", serif;
  font-style: normal;
  font-weight: 400;
  font-size: 1.5em;
  line-height: 2em;
  text-align: center;

  margin: 0 0 0 2%;
  padding: 0;

  color: #000000;
}

.HomeButton:hover {
  background-image: url("../../img/dots.png");
  background-blend-mode: soft-light;
  background-size: contain;
  background-attachment: fixed;
}
